import React from 'react'
import Link from 'gatsby-link'

const Page = () => (
  <div>
    <h1>Visuals</h1>
    <p>Elements of visual design that we seek to use consistently.</p>
    <Link to="/">Go back to the homepage</Link>
  </div>
)

export default Page



